import React, { Component} from 'react'
import { Row, Col, Navbar, Nav, NavDropdown, FormControl, Form ,Button } from 'react-bootstrap';
import './navbar.scss';
export default class MainNavBar extends React.Component{
   
      render() {
        return (
           <Row className="bg_theme">
             <Col md="12" lg="12">
             <Navbar sticky="top"  expand="lg">
              <Navbar.Brand href="#home"><p><strong>Solu</strong>tionsact</p></Navbar.Brand>
              <Navbar.Toggle aria-controls="basic-navbar-nav" />
              <Navbar.Collapse id="basic-navbar-nav">
                <Nav className="mr-auto"></Nav>

                <Nav >
                  <Nav.Link className="activesd" href="#home">Home</Nav.Link>
                  <Nav.Link href="#link">About Us</Nav.Link>
                  <Nav.Link href="#link">Services</Nav.Link>
                  <Nav.Link href="#link">Pages</Nav.Link>
                  <Nav.Link href="#link">Contact Us</Nav.Link>
                
                </Nav>
                
              </Navbar.Collapse>
            </Navbar>
          </Col>
          </Row>  
    
        );
      }
    }
